body {
  background-color: #f5f7f9;
}

.nav {
  background-color: #344050;
  width: 100%;
  height: 64px; 
  position: fixed;
  top: 0;
  z-index: 9999;
}

.header {
  color: white;
  line-height: 64px;
  width: 100%;
  text-align: center;
}

.container-fluid {
  margin-top: 80px;
  height: calc(100vh - 80px);
  display: -ms-flexbox;
  display: flex;
}

.repoList {
  -ms-flex-preferred-size: 100%;
      flex-basis: 100%;
  -webkit-transition: flex-basis 0.5s;
  -o-transition: flex-basis 0.5s;
  transition: flex-basis 0.5s;
  transition: flex-basis 0.5s, -ms-flex-preferred-size 0.5s;
}

.repoList.repo-list--small {
  -ms-flex-preferred-size: 40%;
      flex-basis: 40%;
}

.list-item--repo.active {
  background-color: #cddae8;
}

.list-item--repo.unclickable {
  cursor: not-allowed;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.issueList {
  margin-left: 3rem;
  -ms-flex-preferred-size: 80%;
      flex-basis: 80%;
}

.repoList, .issueList {
  overflow-y: scroll;
}

.gh-avatar {
  width: 100px;
  border-radius: 3px;
}

.list-wrapper, .list-item {
  padding: 0;
  margin-top: 16px;
  list-style-type: none;
}

.list-wrapper.active {
  cursor: move;
}

.list-item {
  position: relative;
  border: 1px solid #ddd;
  border-radius: 3px;
  -webkit-box-shadow: 0 5px 5px -5px #eee;
          box-shadow: 0 5px 5px -5px #eee;
  margin-bottom: 16px;
  background-color: #fff;
  padding: 16px;
  cursor: pointer;
}

.list-item .list-item--text {
  color: #85898d;
  font-size: 16px;
}


